<template>
    <header>
        <span 
            @click="showNav=!showNav"
            :class="{'hamburger':true, 'on':showNav}"><span></span></span>
        <span 
            @click="$router.push({name:'Home'})"
            class="logo">B<span>O</span>M<span>IN</span></span>
        <nav v-show="showNav">
            <ul>
                <li v-for="nav in navs" :key="nav.id">
                    {{nav.display}}
                    <span class="nav-line"></span>
                </li>
            </ul>
        </nav>
    </header>
</template>

<script>
    export default {
        data(){
            return{
                navs:[
                    {display:"JS",name:"JS"},
                    {display:"NODE JS",name:"NODE JS"},
                    {display:"VUE JS",name:"VUE JS"},
                    {display:"OAUTH2",name:"OAUTH2"}
                    ],
                showNav: false
            }
        }
    }
</script>

<style lang="scss" scoped>
    $header-height: 60px;
    $site-color: #34445D;
    header{
        min-height: $header-height;
        background-color: white;
        line-height: $header-height;
        padding: 0px 0px 0px 20px;
        position: fixed;
        font-family: 'Roboto', sans-serif;
        top: 0;
        width: 100%;
        box-sizing: border-box;
        .logo{
            cursor: pointer;
            font-size: 30px;
            font-weight: bold;
            color: $site-color;
            display: inline-block;
            vertical-align: middle;
            span{
                font-weight: 300;
            }
        }
        nav{
            display: block;
            float: right;
            li{
                display: inline-block;
                vertical-align: middle;
                min-width: 120px;
                font-size: 20px;
                text-align: center;
                height: $header-height;
                position: relative;
                cursor: pointer;
                -webkit-user-select: none;
                -khtml-user-select: none;
                -moz-user-select: none;
                -o-user-select: none;
                user-select: none;
                a{
                    color: #222222;
                }
                .nav-line{
                    width: 0px;
                    left: 0;
                    bottom: 0;
                    border-bottom: 3px solid $site-color;
                    display: inline-block;
                    position: absolute;
                    -webkit-transition: width 1s; /* Safari prior 6.1 */
                    transition: width 1s;
                }
                &:hover{
                    background-color: #fefefe;
                    .nav-line{
                        width: 100%;
                    }
                    a{
                        font-weight: bold;
                        color: $site-color;
                    }
                }
            }
        }
        .hamburger {
            display: inline-block;
            vertical-align: middle;
            width: 20px;
            height: 30px;
            margin-right: 10px;
            margin-top: 25px;
            cursor: pointer;
        }

        .hamburger span:after,
        .hamburger span:before {
            content: "";
            position: absolute;
            left: 0;
            top: -9px;
        }
        .hamburger span:after{
            top: 9px;
        }
        .hamburger span {
            position: relative;
            display: block;
        }

        .hamburger span,
        .hamburger span:after,
        .hamburger span:before {
            width: 100%;
            height: 5px;
            background-color: #888;
            transition: all 0.3s;
            backface-visibility: hidden;
            border-radius: 2px;
        }

        /* on activation */
        .hamburger.on span {
            background-color: transparent;
        }
        .hamburger.on span:before {
            transform: rotate(45deg) translate(5px, 5px);
        }
        .hamburger.on span:after {
            transform: rotate(-45deg) translate(7px, -8px);
        }
    }
</style>